* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;
}
span {
  font-weight: 700;
}
.country-list__element {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  font-weight: 400;
}
.country-list__img {
  margin-right: 10px;
}
.country-info__flag {
  margin-right: 10px;
}
